
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  overflow: hidden;
}
* {
  padding: 0;
  margin: 0;
}

.header {
  height: 45px;
  width: 100%;
  position: fixed;
  top: 0px;
  z-index:90;
  /* left: 145px; */
  line-height: 45px;
  background-color: rgb(53, 158, 226)
}
.header>div {
  float: right;
}
.header .menu {
  /* margin-right: 145px; */
  font-size: 20px;
  width: 54px;
  height: 45px;
  line-height: 45px;
  text-align: center;
  color: #fff;
  background-color: #1f70a8;
}
.header .user {
  height: 30px;
  line-height: 30px;
  margin-top: 7px;
  padding-left: 10px;
  padding-right: 10px;
  border-left: 1px solid #1f71a8
}
.header .info {
  margin-right: 10px;
}

.sider{
  position: fixed;
  z-index: 99;
  top: 0px;
  width: 145px;
  height: 100%;
  background-color: rgb(40, 43, 47);
}
.sider .logo {
  width: 145px;
  height: 45px;
  line-height: 45px;
  font-size: 18px;
  font-weight: 400;
  /* padding-left: 20px; */
  color: #fff;
  background-color: rgb(29,29,38);
}
.sider .menu {
  color: aliceblue;
}
.menu-item {
  height: 38px;
  line-height: 38px;
  font-size: 12px;
  padding-left: 16px;
  border-bottom: 1px solid rgb(75, 75, 75);
}
.menu-item>a {
  display: block;
  color: #fff;
  font-size: 12px;
  text-decoration: none;
}

.main {
  margin-left: 145px;
  margin-top: 45px;
  overflow: auto;
  padding: 20px;
  width: 100%;
  height: 100%;
  /* text-align: center; */
}

.footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  font-size: 12px;
  text-align: center;
}























.photo-editor {
  position: relative;
  max-width: 1200px;
  min-width: 800px;
  max-height: 900px;
  min-height: 700px;
  cursor: pointer;
  background: #2f2f2c;
}
.photo-editor .menu-bar {
  position: absolute;
  top: 0;
  left: 0;
  color: #fff;
  font-size: 12px;
  cursor: pointer;
}
.photo-editor .menu-bar .menu {
  position: relative;
  display: inline-block;
  padding: 10px 12px;
}
.photo-editor .menu-bar .menu .logo span {
  color: #50a456;
  -webkit-transition: color 0.2s ease, opacity 0.2s ease;
  transition: color 0.2s ease, opacity 0.2s ease;
}
.photo-editor .menu-bar .menu .logo span {
  text-shadow: rgba(45, 45, 45, 0.5) 0px 0em 0em, rgba(43, 161, 61, 0.35) 0px -0.06em 0.06em, rgba(43, 161, 61, 0.65) 0px -0.12em 0.12em, #ffffff 0px -0.18em 0.18em;
}
.photo-editor .menu-bar .menu .logo-active {
  color: rgba(45, 45, 45, 0.5);
  text-shadow: rgba(45, 45, 45, 0.5) 0px 0em 0em, rgba(43, 161, 61, 0.35) 0px -0.06em 0.06em, rgba(43, 161, 61, 0.65) 0px -0.12em 0.12em, #ff9900 0px -0.24em 0.24em !important;
}
.photo-editor .menu-bar .menu input {
  position: absolute;
  opacity: 0;
  top: 0;
  left: 0;
  width: 72px;
  height: 36px;
  filter: alpha(opacity=0);
  z-index: 99;
}
.photo-editor .menu-bar .menu:hover {
  background-color: rgba(255, 255, 255, 0.1);
}
.photo-editor #tools_left {
  position: absolute;
  width: 50px;
  top: 36px;
  bottom: 0;
  left: 0;
  background: #2F2F2C;
  z-index: 100;
}
.photo-editor #tools_left .menu {
  color: #fff;
  font-size: 14px;
  height: 27px;
  width: 27px;
  border: solid #2f2f2c 8px;
  border-left-width: 13px;
  margin: 0;
}
.photo-editor #tools_left .menu #clipBtn {
  width: 100%;
  height: 100%;
}
.photo-editor #tools_right {
  position: absolute;
  width: 160px;
  height: 100%;
  background: #2f2f2c;
  right: 0;
  top: 0px;
  border-bottom: none;
  overflow: visible;
  padding: 0 0 0 15px;
}
.photo-editor #tools_bottom {
  position: absolute;
  left: 50px;
  right: 0;
  bottom: 0;
  height: 40px;
  overflow: visible;
  background: #2f2f2c;
}
.photo-editor #tools_bottom .menu {
  display: inline-block;
  color: #fff;
  font-size: 16px;
}
.photo-editor #tools_bottom .menu span {
  display: inline-block;
  width: 40px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  background-color: #000;
}
.photo-editor .workarea {
  position: absolute;
  top: 36px;
  left: 50px;
  bottom: 40px;
  right: 175px;
  background-color: #444;
}
.photo-editor .workarea #myCanvas {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  z-index: 1;
  background-color: white;
}
.photo-editor .workarea .canvas-bg {
  width: 600px;
  height: 500px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
}




























































