.subway {
    margin-top: 20px;
    position: relative;
}
/* 线路 */
.subway .lines .line_item{
    margin-bottom:10px;
    font-size:16px;
    color:#000;
    font-weight:bold;
}
/* 车站 */
.subway .station_title {
    display: inline-block;
    width: 100px;
    /* border-bottom: 4px solid #000; */
}
.subway .station_item {
    font-size: 12px;
    margin-bottom: 40px;
    word-wrap: break-word;
    word-break: break-all;
    overflow: hidden;
}
.subway .line {
    position: relative;
    width: 100px;
    border-bottom: 4px solid #000;
    /* border-radius: 4px; */
    margin-top: 15px;
    margin-left:10px;
}
.subway .line::after {
    position: absolute;
    left: -2px;
    bottom: -10px;
    content: '';
    display: inline-block;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: #0089ff;
    opacity: 1;
}
.subway .station_title:nth-last-child(2) .line::before{
    position: absolute;
    right: -2px;
    bottom: -10px;
    content: '';
    display: inline-block;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: #0089ff;
    opacity: 1;
}
.subway .line::after :hover {
    bottom: -8px;
    width: 12px;
    height: 12px;
    opacity: 1;
}
/* 小车 */
.positions .act-point {
    position: absolute;
    bottom: 20px;
    left: -6px;
    width: 50px;
    height: 12px;
    /* border-radius: 50%; */
    background: url('/static/img/tain2.png') no-repeat;
    background: url(data:image/png;base64,) no-repeat;
}
